@import url(https://fonts.googleapis.com/css2?family=Comme:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comme:wght@600&display=swap);
html{background-color:lightcoral}.App{background-color:lightcoral;margin:0 auto;text-align:center;font-family:"Comme", sans-serif}.App__title{color:#fff}

.Carousel{background-color:lightcoral}.Carousel__container{display:flex;justify-content:center;overflow:hidden;margin:30px auto;border:1px solid #fff;border-radius:30px;max-width:1300px}.Carousel__buttons{display:flex;justify-content:center;grid-gap:30px;gap:30px;margin-bottom:20px}.Carousel__list{display:flex;padding:0;margin:0}.Carousel__list li{list-style:none}.Carousel__control{display:flex;justify-content:center;grid-gap:30px;gap:30px;width:-webkit-max-content;width:max-content;border:1px solid #fff;margin:auto;padding:30px;border-radius:30px}.Carousel__labels{display:flex;flex-direction:column;grid-gap:10px;gap:10px;text-align:left;color:#fff;justify-content:space-between}.Carousel__inputs{display:flex;flex-direction:column;justify-content:space-between;grid-gap:20px;gap:20px}.Carousel__input{border-radius:30px;border:none;width:100px;font-size:14px;outline-color:tomato;padding-left:10px}.Carousel__input::-webkit-input-placeholder{font-family:"Comme", sans-serif}.Carousel__input:-ms-input-placeholder{font-family:"Comme", sans-serif}.Carousel__input::placeholder{font-family:"Comme", sans-serif}

